<div class="container"><el-row :gutter="88"><el-col :span="8"><el-button @click="createNewObj()">新增一级目录</el-button><el-tree :data="treeData" node-key="id" default-expand-all :expand-on-click-node="false"><template #default="{ node, data }"><span class="custom-tree-node"><span>{{ node.label }}</span><span><el-button v-if="data.type === 'bar'" type="text" size="mini" @click="() =&gt; append(data)">添加</el-button><el-button v-if="!data.children || data.children.length &lt;= 0" type="text" size="mini" @click="() =&gt; remove(node, data)" style="color: red">删除</el-button></span></span></template></el-tree></el-col><el-col :span="8" v-if="showEditMenu"><el-form inline label-width="100px" label-position="left"><el-form-item label="上级菜单：" v-if="showParentSelect"><el-select v-model="form.parentId" style="width: 385px" placeholder="请选择上级菜单" :disabled="parentDisabled"><el-option v-for="item in parentData" :key="item.id" :value="item.id" :label="item.label"></el-option></el-select></el-form-item><el-form-item label="菜单名称："><el-input v-model="form.label" style="width: 385px" placeholder="请输入菜单名称"></el-input></el-form-item><el-form-item label="菜单英文："><el-input v-model="form.name" style="width: 385px" placeholder="请输入菜单英文"></el-input></el-form-item><el-form-item label="菜单路径："><el-input v-model="form.path" style="width: 385px" placeholder="请输入菜单路径"></el-input></el-form-item><el-form-item label="菜单类型："><el-select v-model="form.type" style="width: 385px" placeholder="请选择菜单类型"><el-option label="可展开菜单" value="bar"></el-option><el-option label="不可展开菜单" value="menu"></el-option></el-select></el-form-item><br><el-form-item class="foot-btns" style="margin-top: 40px"><el-button @click="showEditMenu = false">关闭</el-button><el-button @click="resetForm()">重置</el-button><el-button type="success" @click="confirmHandle()">确定</el-button></el-form-item></el-form></el-col></el-row></div>